<template>
  <div class="goods">
    <!--  -->
    <div class="header">
      <el-input
        v-model="input"
        placeholder=""
        @change="searchInput"
      ></el-input>
      <el-button type="primary"></el-button>
      <el-button type="primary">
        <router-link to="/layout/addgoods" style="color: #fff"
          ></router-link
        >
      </el-button>
      <el-button type="primary" @click="addGoods" style="color: #fff"
        ></el-button
      >
    </div>
    <!--  -->
    <div class="wrapper">
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        @selection-change="handleSelectionChange"
        @row-click="handleRowClick"
        ref="handSelectTest_multipleTable"
        row-key="id"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="id" label="ID" width="100">
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
          width="100"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column prop="price" label="" width="100">
        </el-table-column>
        <el-table-column prop="num" label="" width="100">
        </el-table-column>
        <el-table-column
          prop="category"
          label=""
          width="100"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column prop="image" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="sellPoint"
          label=""
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column prop="descs" label="" show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="" width="280">
          <template slot-scope="scope">
            <el-button size="mini" icon="el-icon-search"></el-button>
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              type="primary"
              icon="el-icon-edit"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              icon="el-icon-delete"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-button
        type="danger"
        icon="el-icon-delete"
        @click="delAll"
        :disabled="multipleSelection.length === 0"
        style="margin: 15px"
      >
        
      </el-button>
    </div>
    <!--  -->
    <Mypagination
      :total="total"
      :pageSize="pageSize"
      @changePage="changePage"
      :current-page="currentPage"
    />

    <!-- <GoodsDialog :dialogVisible='dialogVisible' @close='close'/> -->
    <GoodsDialog ref="dialog" :editOrAdd="editOrAdd" :rowData="rowData" />
  </div>
</template>

<script>
import Mypagination from "@/components/Mypagination.vue";
import GoodsDialog from "./GoodsDialog.vue";
import { MessageBox } from "element-ui";
export default {
  data() {
    return {
      input: "",
      tableData: [],
      total: 10,
      pageSize: 1,
      type: 1,
      list: [],
      dialogVisible: false,
      currentPage: 1,
      editOrAdd: "",
      rowData: {},
      multipleSelection: [],
    };
  },
  methods: {
    // 
    //
    handleSelectionChange(val) {
      console.log(val); //
      this.multipleSelection = val;
    },
    //
    handleRowClick(row, column, event) {
      this.$refs.handSelectTest_multipleTable.toggleRowSelection(row);
      console.log(row); //
    },
    //
    delAll() {
      var arr = []; //
      //id
      this.multipleSelection.forEach(row => arr.push(row.id));

      console.log(
        "id",
        this.multipleSelection.forEach(row => arr.push(row.id))
      );
      this.$confirm("", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "success",
        callback: action => {
          if (action === "confirm") {
            //
            this.$api
              .bathDelete({
                id: arr,
              })
              .then(res => {
                console.log(res.data);
                this.$notify({
                  title: "",
                  message: "",
                  type: "success",
                  duration: 2000,
                });
                //
                this.http(1);
              })
              .catch(() => {
                console.log("error submit");
              });
          }
        },
      });
    },
    close() {
      this.dialogVisible = false;
    },
    //  
    addGoods() {
      this.editOrAdd = "";
      // this.dialogVisible = true
      this.$refs.dialog.dialogVisible = true;
    },
    // 
    searchInput(val) {
      // 1
      if (!val) {
        this.http(1);
        return;
      }
      console.log(val);
      this.$api
        .getSearch({
          search: val,
        })
        .then(res => {
          console.log(res.data);
          if (res.data.status === 200) {
            this.currentPage = 1;
            this.list = res.data.result;
            this.total = res.data.result.length;
            this.pageSize = 3; // 
            this.tableData = res.data.result.slice(0, 3);
            this.type = 2; //
          } else {
            this.tableData = [];
            // this.type=1
            this.total = 1;
            this.pageSize = 1;
            this.type = 1;
          }
        });
    },

    // 
    handleEdit(index, row) {
      console.log(index, row);
      this.editOrAdd = "";
      // 
      this.$refs.dialog.dialogVisible = true;

      // 
      this.rowData = { ...row };
    },
    // 
    handleDelete(index, row) {
      console.log(index, row);
      MessageBox.confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$api
            .delGoods({
              id: row.id,
            })
            .then(res => {
              console.log(res.data);
              if (res.data.status === 200) {
                this.$message({
                  type: "success",
                  message: "!",
                });
                this.http(1);
              } else {
                this.$message({
                  type: "error",
                  message: "!",
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    changePage(page) {
      this.currentPage = page; // 
      this.http(page);
      if (this.type === 1) {
        this.http(page);
      } else {
        this.tableData = this.list.slice((num - 1) * 3, num * 3);
      }
    },
    http(page) {
      this.$api
        .getGoodsList({
          page,
        })
        .then(res => {
          console.log(res.data);
          if (res.data.status === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
            this.pageSize = res.data.pageSize;
          }
        });
    },
  },
  mounted() {
    this.http(1);
  },
  components: { Mypagination, GoodsDialog },
};
</script>

<style lang="scss" scoped>
.goods {
  margin: 20px;
  .header {
    display: flex;
    button {
      margin-left: 20px;
    }
  }
}
.wrapper {
  margin: 20px 0;
}
</style>
