<template>
  <div style="border: 1px solid #ccc">
    <Toolbar
      style="border-bottom: 1px solid #ccc"
      :editor="editor"
      :defaultConfig="toolbarConfig"
      :mode="mode"
    />
    <Editor
      style="height: 300px; overflow-y: hidden"
      v-model="html"
      :defaultConfig="editorConfig"
      :mode="mode"
      @onCreated="onCreated"
      @onChange="onChange"
    />
  </div>
</template>
<script>
import Vue from "vue";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";
export default Vue.extend({
  components: { Editor, Toolbar },
  data() {
    return {
      editor: "",
      html: "",
      toolbarConfig: {},
      editorConfig: { placeholder: "..." },
      mode: "default", // or 'simple'
    };
  },
  methods: {
    // 
    onChange(content) {
      console.log("onChange", content.children[0].children[0].text);
      this.$emit("getEditorContent", content.children[0].children[0].text);
    },

    onCreated(editor) {
      this.editor = Object.seal(editor); //  Object.seal() 
    },
  },
  mounted() {
    // this.editorConfig.onchange = (newHtml) => {
    //   console.log("change", newHtml);
    // };
    //  ajax 
  },

  beforeDestroy() {
    const editor = this.editor;
    if (editor == null) return;
    editor.destroy(); // 
  },
});
</script>
<style src="@wangeditor/editor/dist/css/style.css"></style>
