<template>
  <!--  -->
  <el-dialog
    :title="editOrAdd == '' ? '' : '1'"
    :visible.sync="dialogVisible"
    width="70%"
    :before-close="clearFrom"
  >
    <!--  -->
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="category">
        <el-button type="primary" @click="innerVisible = true"
          >{{ ruleForm.category ? ruleForm.category : "" }}
        </el-button>
      </el-form-item>

      <el-form-item label="" prop="title">
        <el-input v-model="ruleForm.title"></el-input>
      </el-form-item>

      <el-form-item label="" prop="price">
        <el-input v-model="ruleForm.price"></el-input>
      </el-form-item>

      <el-form-item label="" prop="num">
        <el-input v-model="ruleForm.num"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="5">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="ruleForm.date1"
          ></el-date-picker>
        </el-col>

        <el-col :span="5">
          <el-time-picker
            placeholder=""
            v-model="ruleForm.date2"
          ></el-time-picker>
        </el-col>
      </el-form-item>

      <el-form-item label="" prop="sellPoint">
        <el-input v-model="ruleForm.sellPoint"></el-input>
      </el-form-item>

      <el-form-item label="" prop="image">
        <el-button type="primary" @click="innerVisibleImg = true"
          ></el-button
        >
        <img :src="ruleForm.image" height="200px" alt="" />
      </el-form-item>

      <el-form-item label="" prop="descs">
        <WangEditor ref="myEditor" @getEditorContent="getEditorContent" />
      </el-form-item>

      <el-form-item label="" v-show="isShow"> </el-form-item>
      <el-form
        ref="dynamicValidateForm"
        label-width="120px"
        class="demo-dynamic"
      >
        <el-form-item
          v-for="(item, index) in groups"
          :label="item.title"
          :key="index"
          :prop="item.value"
        >
          <el-input v-model="item.value"></el-input>

          <el-form-item
            v-for="(ele, i) in item.chiledern"
            :label="ele.title"
            :key="i"
            :prop="ele.value"
          >
            <el-input v-model="ele.value"></el-input>
          </el-form-item>
        </el-form-item>
      </el-form>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="submitForm"> </el-button>
    </span>

    <!-- 1   -->
    <el-dialog
      width="40%"
      title=""
      :visible.sync="innerVisible"
      append-to-body
    >
      <TreeGoods @sendTreeData="sendTreeData" />

      <span slot="footer" class="dialog-footer">
        <el-button @click="innerVisible = false"> </el-button>
        <el-button type="primary" @click="showTreeData"></el-button>
      </span>
    </el-dialog>

    <!-- 2   -->
    <el-dialog
      width="40%"
      title=""
      :visible.sync="innerVisibleImg"
      append-to-body
    >
      <UploadImg @sendImg="sendImg" />

      <span slot="footer" class="dialog-footer">
        <el-button @click="clearFrom"> </el-button>
        <el-button type="primary" @click="showImg"></el-button>
      </span>
    </el-dialog>
  </el-dialog>
</template>

<script>
import { val } from "dom7";
import TreeGoods from "./TreeGoods.vue";
import UploadImg from "./UploadImg.vue";
import WangEditor from "./WangEditor.vue";
export default {
  props: {
    editOrAdd: {
      type: String,
      default: "",
    },
    rowData: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  // props:['editOrAdd','rowData'],

  data() {
    return {
      isShow: false, // 
      dialogVisible: false,
      innerVisible: false,
      innerVisibleImg: false,
      imgUrl: "",
      treeData: {},
      ruleForm: {
        id: "",
        title: "",
        price: "",
        num: "",
        sellPoint: "",
        image: "",
        descs: "",
        cid: "",
        category: "",
        date1: "",
        date2: "",
      },
      groups: [],
      rules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        price: [{ required: true, message: "", trigger: "blur" }],
        num: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  watch: {
    rowData(newValue, oldValue) {
      console.log("", newValue);
      this.ruleForm = newValue;
      // 
      // this.$refs.myEditor.clearContent();
      this.$nextTick(() => {
        // this.$refs.myEditor.editor.text.html(newValue.descs);
        console.log("this.$refs.myEditor", this.$refs.myEditor);
        // this.$refs.myEditor.editor.text.html(newValue.descs);
      });
      if (val.paramsInfo) {
        this.isShow = true;
        this.groups = JSON.parse(val.paramsInfo);
      }
    },
  },
  methods: {
    // 1.1 
    getEditorContent(content) {
      this.ruleForm.descs = content;
    },
    showImg() {
      this.innerVisibleImg = false;
      this.ruleForm.image = this.imgUrl;
    },

    showTreeData() {
      this.innerVisible = false;
      this.ruleForm.category = this.treeData.name;
      this.ruleForm.cid = this.treeData.cid;
      // this.isShow = true;

      this.$api
        .categoryData({
          cid: this.treeData.cid,
        })
        .then(res => {
          console.log("res", res.data);

          if (res.data.status === 200) {
            this.isShow = true;
            let result = res.data.result[res.data.result.length - 1];
            this.groups = JSON.parse(result.paramData);
          } else {
            this.isShow = false;
          }
          // let data = res.data.data;
          // let arr = [];
          // data.forEach((item) => {
          //   if (item.id == this.ruleForm.cid) {
          //     arr = item.params;
          //   }
          // });
          // console.log("arr", arr);
          // this.isShow = true;
        });
    },
    //
    sendTreeData(data) {
      console.log(data);
      this.treeData = data;
    },
    close() {
      this.$emit("close");
    },
    submitForm() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          // alert("submit!");
          console.log("", this.ruleForm);
          let {
            title,
            cid,
            category,
            sellPoint,
            price,
            num,
            descs,
            image,
            id,
          } = this.ruleForm;
          // 
          if (this.editOrAdd === "") {
            this.$api
              .addGoods({
                title,
                cid,
                category,
                sellPoint,
                price,
                num,
                descs,
                image,
                paramsInfo: JSON.stringify(this.groups),
              })
              .then(res => {
                console.log("----", res.data);
                if (res.data.status == 200) {
                  this.$parent.http(1);
                  this.$message({
                    type: "success",
                    message: "!",
                  });
                  this.clearFrom();
                }
              });
          } else {
            console.log("", id);
            this.$api
              .updateGoods({
                id,
                title,
                cid,
                category,
                sellPoint,
                price,
                num,
                descs,
                image,
              })
              .then(res => {
                console.log("----", res.data);
                if (res.data.status == 200) {
                  this.$parent.http(1);
                  this.$message({
                    type: "success",
                    message: "!",
                  });
                  this.clearFrom();
                }
              });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      // this.$refs[formName].resetFields();
      this.ruleForm = {};
    },
    //  
    sendImg(data) {
      // this.ruleForm.image = data;
      console.log(data);
      this.imgUrl = data;
    },
    clearFrom() {
      this.ruleForm = {}; // 
      this.dialogVisible = false; //  
    },
  },
  components: { TreeGoods, UploadImg, WangEditor },
};
</script>

<style lang="scss" scoped></style>
